
















.banner {
  @apply bg-cover bg-center flex justify-center items-center text-center;
  height: 590px;

  > .container {
    > * {
      @apply text-white mx-auto;
      max-width: 800px;
    }

    > h6 {
      @apply leading-normal mb-16;
    }

    > .btn {
      min-width: 255px;
    }
  }
}
